.cross_type_third {
    width: 50px;
    height: 50px;
    grid-area: cross3;
    align-self: start;
    justify-self: start;
    animation: rotate 20s linear infinite;
    background-image: url(../../../images/cross-third.svg);
}

@keyframes rotate {
    to {
        transform: rotate(360deg);
    }
}